.digital-dentistry {
  padding: 0px 60px 100px;
  .digital-dentistry-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    .digital-dentistry-header {
      // font-size: 38px;
      padding: 100px 50px 20px;
      font-size: 52px;
      font-weight: 400;
      span {
        color: #ba9e66;
      }
    }
    .digital-dentistry-container {
      display: grid;
      grid-template-columns: 1fr;
      align-items: center;
      gap: 10px;
      position: relative;
      .digital-dentistry-cont {
        display: flex;
        flex-direction: column;
        gap: 10px;
      }
      // border-left: 2px solid #ba9e66;
      // border-right: 2px solid #ba9e66;
      .digital-dentistry-desc {
        padding: 30px 20px;
        display: flex;
        flex-direction: column;
        gap: 40px;
        border-bottom: 2px solid #ba9e66;
        cursor: pointer;
        &:hover {
          .digit-head {
            background-size: 100% 2px;
          }
        }
        .digit-header {
          font-size: 24px;
          padding: 0;
          display: flex;
          gap: 10px;
          align-items: center;
          justify-content: space-between;
        }
        .digit-head {
          background-position: 0 100%;
          background-repeat: no-repeat;
          background-image: linear-gradient(currentColor, currentColor);
          justify-self: right;
          background-size: 0 2px;
          transition: background-size 0.5s;
          position: relative;
        }
        .digital-dentistry-description {
          background: linear-gradient(
            rgba(255, 255, 255, 0.508),
            // rgba(186, 158, 102, 0.333),
            // rgba(186, 158, 102, 0.331),
            rgba(255, 255, 255, 0.508)
          );
          padding: 40px;
          color: #fff;
          // color: #000;
          font-family: "Proza Libre", sans-serif;
          font-size: 18px;
          line-height: 2;
          text-align: center;
          letter-spacing: 0.5px;
          font-weight: 400;
          // color: rgb(61, 61, 60);
          // color: #2d2d2d;
        }
        .digital-dentistry-image {
          grid-row: 1/-1;
          padding: 20px;
          img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            position: relative;
          }
          img::after {
            content: " ";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            background: linear-gradient(
              rgba(0, 0, 0, 0.516),
              rgba(0, 0, 0, 0.516)
            );
          }
        }
        .digital-dentistry-w {
          display: none;
        }
        .digitaldent {
          // display: flex;
          // align-items: center;
          padding: 20px;
          display: flex;
          position: relative;
          height: 500px;
          // flex-direction: row-reverse;
          align-items: center;
          // animation: ;
        }
        @keyframes imageDigit {
          0%,
          25% {
            margin: 25px;
            height: 200px;
          }
          50%,
          100% {
            margin: 25px;
            height: 400px;
          }
        }
        .digit-svg-down {
          transform: rotate(90deg);
          display: flex;
          align-items: center;
          transition: 1s transform;
        }
        .digit-svg {
          transform: rotate(270deg) !important;
        }
      }
    }
  }
}

