.home-section-5-wrap {
  padding: 100px 0px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  position: relative;
  // z-index: -1;
  // background-color: #000;
}
.home-section-5-container {
  display: flex;
  flex-direction: column;
  color: #000;
  .home-section-5-header {
    display: flex;
    flex-direction: column;
    align-items: left;
    .home-section-5-heading {
      display: flex;
      font-size: 48px;
      margin-bottom: 20px;
      padding-left: 230px;
      span {
        font-weight: 600;
        color: #ba9e66;
      }
    }
  }
}
.home-section-5-smallh {
  font-size: 52px;
}
.home-section-5-list {
  display: flex;
  flex-direction: column;
  font-size: 28px;
  line-height: 1.5;
  li {
    list-style-type: none;
    display: flex;
    align-items: center;
    gap: 14px;
  }
}
.home-section-5-image-container {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 600px;
  height: 470px;
  top: 0%;
  left: 0%;
  // background-image: url(../../../../assets/gallery-2.jpg);
  background-image: url(../../../../assets/digital-3d-imaging.jpg);
}
