.bilimdent-kids {
  padding: 100px;
}
.bilim-dent-kids-header {
  margin-bottom: 40px;
  font-size: 52px !important;
  text-align: center;
  font-weight: 500;

  span {
    color: #ba9e66;
  }
}
.bilimdent-kids-container {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.bilimdent-kids-inner {
  display: flex;
  text-align: center;
  gap: 30px;
  padding: 40px;
  align-items: center;
  // border: 1px solid #000;
  transition: all 0.5s;
}
.bilimdent-kids-desc::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65));
}
.desc-image-1 {
  background-image: url(../../../assets/kids/pedadontics.jpg);
}
.desc-image-2 {
  background-image: url(../../../assets/kids/firstExamination.jpg);
}
.desc-image-3 {
  background-image: url(../../../assets/kids/tooth-eruption.jpg);
}
.desc-image-4 {
  background-image: url(../../../assets/kids/tooth-treatments.jpg);
}
.desc-image-5 {
  background-image: url(../../../assets/kids/tooth-decay.jpg);
}
.desc-image-6 {
  background-image: url(../../../assets/kids/tooth-fracture.jpg);
}
.desc-image-7 {
  background-image: url(../../../assets/kids/tooth-disclosing.jpg);
}
.desc-image-8 {
  background-image: url(../../../assets/kids/clear-aligners.jpg);
}
.desc-image-9 {
  background-image: url(../../../assets/kids/feasure-sealant.jpg);
}
.desc-image-10 {
  background-image: url(../../../assets/kids/general-anestesia.jpg);
}
.bilimdent-kids-icon {
  background: linear-gradient(
    rgba(255, 255, 255, 0.508),
    // rgba(186, 158, 102, 0.333),
    // rgba(186, 158, 102, 0.331),
    rgba(255, 255, 255, 0.508)
  );
  padding: 20px;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  position: relative;
  gap: 40px;
  p {
    text-align: center;
  }
  div {
    display: none;
    width: 300px;
    height: 300px;
    // position: absolute;
    // background: linear-gradient(
    //   rgba(255, 255, 255, 0.509),
    //   rgba(255, 255, 255, 0.509)
    // );
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
}
.bilimdent-kids-desc {
  background-size: cover;
  background-position: center;
  position: relative;
  z-index: 5;
  padding: 40px;
  color: #fff;
  h3 {
    font-size: 42px !important;
    font-weight: 400;
    background: linear-gradient(
      rgba(244, 244, 244, 0.508),
      // rgba(186, 158, 102, 0.333),
      // rgba(186, 158, 102, 0.331),
      rgba(255, 255, 255, 0.508)
    );
    padding: 40px;
  }
  p {
    // color: #777777;
    font-family: "Proza Libre", sans-serif;
    font-size: 20px;
    line-height: 1.8;
  }
}
.bilimdent-kids-outer {
  display: flex;
  flex-direction: column;
  gap: 40px;
  overflow-x: hidden;
  .bilimdent-kids-outer-head {
    font-size: 48px;
  }
  .bilimdent-kids-outer-container {
    width: 3300px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
    // background-color: red;
    // transform: translateX(40%);
    // transform: translateX(-85%);

    animation: goround 30s linear infinite;
    .bilimdent-kids-outer-description {
      display: flex;
      // margin-left: 70px;
      align-items: center;
      font-size: 18px;
      font-weight: 400;
      // gap: 60px;
      font-family: "Proza Libre", sans-serif;

      letter-spacing: -0.1px;
      div {
        display: flex;
        // background-color: blue;
        margin-left: 30px;
        margin-right: 30px;
      }
    }
  }
  .bilimdent-kids-outer-desc {
    font-size: 18px;
    line-height: 1.7;
    color: #777;
    font-family: "Proza Libre", sans-serif;
  }
  @keyframes goround {
    from {
      transform: translateX(40%);
    }
    to {
      transform: translateX(-85%);
    }
  }
}
