.blog-antalya {
  padding: 0 100px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  .blog-antalya-header {
    font-size: 52px !important;
  }
  .blog-antalya-container {
    display: flex;
    gap: 80px;
    .blog-antalya-image-container {
    }
    .blog-antalya-description-container {
      display: flex;
      flex-direction: column;
      .blog-antalya-description {
        h2 {
          font-size: 42px;
          color: #ba9e66;
        }
        p {
          font-family: "Proza Libre", sans-serif;
          color: #777;
          font-size: 18px !important;
        }
      }
      .blog-antalya-image-con {
        img {
          width: 240px !important;
          height: 150px !important;
        }
      }
    }
  }
  .blog-antalya-image-con {
    display: flex;
    align-items: center;
    gap: 80px;
  }
  .blog-antalya-video-container {
    iframe {
      width: 560px;
      height: 315px;
    }
  }
}
