.home-section-2 {
  // background-color: #000;
  background-color: #fff;
  color: #000;
  // color: #fff;
}
.home-section-2-wrapper {
  padding: 100px 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0px;
}
.home-section-2-description {
  font-size: 28px;
  line-height: 1.5;
  display: flex;
  padding: 0px 120px;
  border-right: 1px solid #fff;
  // padding-right: 40px;
  p {
    text-align: center;
  }
}
.home-section-2-adv {
  font-size: 28px;
  display: flex;
  // align-items: center;
  flex-direction: column;
  position: relative;
  // background-color: red;
  // height: 28px * 4;
  gap: 20px;
  align-items: center;
  .adv-free {
    // height: 28px * 1.5;
    padding: 0 20px;
    // font-size: 3px;
  }
  .adv-description {
    // display: flex;
    // flex-direction: column;
    // padding: 0px 20px;
    // border-bottom: 1px solid #fff;
    // gap: 40px;
    // height: 28px * 1.5;
    overflow: hidden;

    // text-align: center;
    .adv-description-inner {
      display: flex;
      gap: 40px;
      // animation: rotate 6s ease-in-out infinite;
      .hover-underline-animation {
        margin-bottom: 4px;
        // padding: 20px;
        display: block;
        font-weight: 700;
        color: #ba9366;
      }
    }
  }
}
@keyframes rotate {
  0%,
  20% {
    transform: translateY(0);
  }
  25%,
  45% {
    transform: translateY(28px * -1.35);
  }
  50%,
  70% {
    transform: translateY(28px * -2.68);
  }
  75%,
  95% {
    transform: translateY(28 * 3.7);
  }
}
.hover-underline-animation {
  display: inline-block;
  position: relative;
}

