.about-us {
}
.about-container-1 {
  padding: 0px 100px 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 0;
}
.about-us-gallery {
  width: 1400px;
  height: 500px;
  align-items: center;
  position: relative;

  .about-us-gal {
    overflow: hidden;
  }
  .gallery-arrow-left {
    position: absolute;
    align-items: center;
    display: flex;
    justify-content: center;
    // height: 400px;
    height: 100%;
    width: 100px;
    top: 63.3%;
    left: 33%;
    z-index: 100;
    // background-color: #ba9e66;
    cursor: pointer;
    svg {
      background-color: #ba9e66;
      color: #fff;
      padding: 14px;
    }
  }
  .gallery-arrow-right {
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100px;
    top: 63.3%;
    right: 33%;
    transform: rotate(180deg);
    z-index: 1;
    cursor: pointer;
    svg {
      padding: 14px;
      background-color: #ba9e66;
      color: #fff;
    }
  }
}

.about-us-gallery-container {
  display: flex;
  width: 7000px;
  height: 600px;
  transform: translateX(-5600px);
  .clinic-about-us {
    // background-color: gray;
  }
  div {
    // width: 100%;
    // height: 100%;
    width: 700px;
    height: 600px;
    // display: none;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      // cursor: url(../../../assets/plus.svg), nesw-resize;
      transition: all 0.9s;
    }
    img:hover {
      // transform: scale(1.2);
    }
  }
}
.about-header {
  margin: 0;
  padding: 80px 80px 80px;
  color: #000;
  font-weight: 500;

  font-size: 52px;
}
.gallery-num {
  position: absolute;
  bottom: -20%;
  left: 47.5%;
  // width: 30px;
  padding: 20px;
  font-size: 24px;
  background-color: #ba9e66;
  color: #fff;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.about-container-2 {
  margin-top: 100px;
  padding: 0px 100px;
  padding-bottom: 100px;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about-us-team {
  justify-content: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}
.about-team-wrap {
  // background-color: #000;
  // background-color: #ba9e66;
  display: flex;
  // width: 520px;
  flex-direction: column;
  color: #000;
  border: 1px solid #ba9e66;
  padding: 40px 60px;
}
.about-team-wrap:last-child {
  grid-column: 1 / -1;
}
.about-team-header {
  font-size: 42px !important;
}
.about-team-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  h4 {
    font-size: 20px;
  }
}
.about-team-row-1 {
  // display: flex;
  gap: 20px;
  display: flex;
  align-items: center;
}
.about-team-row-2 {
  display: flex;

  align-items: center;
  gap: 36px;
}
.about-team-row-3 {
  display: flex;
  align-items: center;
  justify-content: center;

  gap: 28px;
}
.about-us-gallery-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.about-container-3 {
  display: flex;
  padding: 0px 100px;
  padding-bottom: 100px;
  flex-direction: column;
  align-items: center;
}
.about-tech-wrap {
  display: flex;
  flex-direction: column;
  gap: 60px;
}

.about-tech-container {
  display: grid;
  font-size: 22px;
  font-weight: 700;
  text-align: center;
  gap: 20px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  div:nth-child(2) {
    color: #ba9e66;
  }
  div:nth-child(5) {
    color: #ba9e66;
  }
  div:nth-child(7) {
    color: #ba9e66;
  }
  div:nth-child(4) {
    color: #ba9e66;
  }
}
.about-tech-container-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0px;
  .tech-wrapper {
  }
}
.tech-container {
  display: flex;
  padding: 10px 40px;
  flex-direction: column;
  gap: 0px;
  div {
    // display: none;
    line-height: 1.8;
    font-size: 16px;
    color: #777777;
    font-family: "Proza Libre", sans-serif;
  }
}
.tech-wrapper:nth-last-child(odd) {
  // background-color: red;
  border-right: 2px solid #ba9e66;
}

