.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 1074px;
  // box-shadow: 0 4rem 6rem rgba(0, 0, 0, 0.3);
  z-index: 1000;
  transition: all 0.5s;
  display: flex;
  gap: 40px;
  align-items: center;

  .image-container {
    width: 1000px;
    height: 600px;
    // background-image: url("../../../assets//clinic-9.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.797);
  backdrop-filter: blur(4px);
  z-index: 100;
  transition: all 0.5s;
}

.btn--close-modal {
  font-family: inherit;
  color: inherit;
  position: absolute;
  top: 10%;
  right: 10%;
  font-size: 4rem;
  cursor: pointer;
  border: none;
  background: none;
  color: #fff;
}
.modal-btn-left {
  width: 300px;
  height: 300px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  svg {
    transform: rotate(180deg);
  }
}
.modal-btn-right {
  width: 300px;
  height: 300px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-num {
  // background-color: red;
  color: #fff;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  position: absolute;
  bottom: 10%;
  left: 48%;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}
