.home-section-3-wrap {
  padding: 100px 60px;
  padding-bottom: 120px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  .section-3-heading {
    font-size: 52px;
    font-weight: 700;
  }
  .section-3-list {
    display: flex;
    gap: 60px;
    font-weight: 500;
    div {
      line-height: 2;
    }
    li {
      list-style-type: none;
      font-size: 28px;
      display: flex;
      align-items: center;
      gap: 20px;
    }
  }
}
.section-3-info-button {
  cursor: pointer;
  font-size: 28px;
  font-weight: 600;
  margin-top: 50px;
  padding: 20px;
  text-align: center;
  background-color: #ba9e66;
  position: relative;
  color: #fff;
  z-index: 1;
}
.section-3-info-button::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scaleY(0);
  transform-origin: center bottom;
  background: #000;
  z-index: -1;
  transition: transform 0.3s;
}

.section-3-info-button:hover::after {
  color: #fff;
  transform: scaleY(1);
}
@media (max-width: 1366px) {
  .home-section-3-wrap {
    padding: 30px 50px;
    padding-bottom: 100px;
    gap: 20px;
    .section-3-heading {
      font-size: 45px !important;
    }
    .section-3-list {
      gap: 30px;
      li {
        font-size: 25px !important;
        gap: 18px;
      }
    }
  }
  .section-3-info-button {
    font-size: 25px !important;
    margin-top: 40px;
    padding: 16px;
  }
  .section-3-image-container {
    img {
      width: 490px;
      height: 600px;
    }
  }
}
@media (max-width: 1200px) {
  .home-section-3-wrap {
    padding: 20px 40px;
    padding-bottom: 80px;
    gap: 20px;
    .section-3-heading {
      font-size: 38px !important;
    }
  }
  .section-3-list {
    gap: 30px !important;
    li {
      // font-size: 20px;
      font-size: 22px !important;

      gap: 16px;
    }
  }
  .section-3-info-button {
    font-size: 22px !important;
    margin-top: 30px;
    padding: 12px 8px;
  }
  .section-3-image-container {
    img {
      width: 320px;
      height: 420px;
    }
  }
}
@media (max-width: 944px) {
  .home-section-3-wrap {
    padding: 10px 20px;
    padding-bottom: 60px;
    .section-3-heading {
      font-size: 31px !important;
    }
  }
  .section-3-list {
    // gap: 10px;
    gap: 20px !important;
    li {
      font-size: 20px !important;
      gap: 12px;
      svg {
        width: 24px;
        height: 24px;
      }
    }
  }
  .section-3-info-button {
    font-size: 20px !important;
    margin-top: 20px;
    padding: 12px 8px;
  }
  .section-3-image-container {
    align-self: start;
    display: none;
    img {
      width: 220px;
      height: 300px;
    }
  }
}
@media (max-width: 704px) {
  .home-section-3-wrap {
    padding: 10px 20px;
    padding-bottom: 60px;
    .section-3-heading {
      font-size: 28px !important;
    }
  }
  .section-3-list {
    // gap: 10px;
    gap: 30px;
    li {
      font-size: 18px !important;
      gap: 8px !important;
      svg {
        width: 16px;
        height: 16px;
      }
    }
  }
  .section-3-info-button {
    font-size: 18px !important;
    margin-top: 20px;
    padding: 12px 8px;
  }
  .section-3-image-container {
    img {
      width: 220px;
      height: 280px;
    }
  }
}
@media (max-width: 560px) {
  .home-section-3-wrap {
    padding: 10px 0px !important;
    padding-bottom: 40px !important;
    align-items: center;
    .section-3-heading {
      font-size: 24px !important;
    }
  }
  .section-3-description {
    align-items: center;
    display: flex;
    flex-direction: column;
  }
  .section-3-list {
    // background-color: blue;
    // gap: 10px;
    gap: 1px !important;
    display: grid !important;
    align-items: center !important;
    justify-content: center !important;
    grid-template-columns: 1fr;
    padding: 0 50px !important;
    height: 200px;
    overflow-y: auto;
    li {
      font-size: 14px !important;
      gap: 20px !important;
      text-align: center;
      display: block !important;
      svg {
        display: none;
        width: 20px;
        height: 20px;
      }
    }
  }
  .section-3-info-button {
    justify-self: center;
    font-size: 14px;
    width: 200px;
    margin-top: 40px;
    padding: 12px 8px;
  }
  .section-3-image-container {
    img {
      width: 140px;
      height: 180px;
    }
  }
}
