.home-section-7 {
  padding: 20px 60px 100px;
}
.section-7-head {
  text-align: center;
  font-size: 52px;
  padding: 20px 20px 40px;
}
.home-section-7-wrap {
  display: flex;
  align-items: baseline;
  gap: 60px;
  padding: 20px;
}
.section-7-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
  // text-align: center;
  height: 600px;

  padding: 40px;
  // background-color: #000;
  background-color: #000;
  // background-color: #ba9e66;
  // background-color: #fff;
  color: #fff;
  // color: #ba9e66;
}
.section-7-wrap-header {
  font-size: 32px;
}
.section-7-container::-webkit-scrollbar {
  width: 0;
}
.section-7-wrap-container {
  display: flex;
  flex-direction: column;
}
.section-7-wrap-title {
  font-size: 28px;
  font-weight: 700;
}
.section-7-description {
  font-size: 24px;
  line-height: 1.5;
}
@media (max-width: 1366px) {
  .home-section-7 {
    padding: 50px 50px !important;
  }
  .section-7-head {
    font-size: 45px !important;
  }
  .home-section-7-wrap {
    padding: 14px !important ;
  }
  .section-7-container {
    height: 380px !important;
  }
  .section-7-wrap-header {
    font-size: 36px !important;
  }
  .section-7-wrap-title {
    font-size: 20px !important;
  }
  .section-7-description {
