.home-section-6 {
  padding: 100px 50px 100px;
  background-color: #fff;
  // background-color: #000;
  // color: #fff;
  color: #000;
}
.home-section-6-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
}
.home-section-6-heading {
  font-size: 52px;
}
.home-section-6-container {
}
.home-section-6-user-reviews {
  position: relative;
  // // width: 770px;
  // overflow-x: auto;
  // display: flex;
  // justify-content: left;
  // align-items: center;
  // overflow-y: hidden;
  .home-section-6-user-reviews-inner {
    display: grid;
    grid-template-columns: 1fr;
    padding-left: 20px;
    display: flex;
    // flex-wrap: wrap;
    margin-top: 20px;
    align-items: center;
    justify-content: center;
    gap: 30px;
  }
  &.home-section-6-user-reviews::-webkit-scrollbar {
    // display: none;
    width: 0px;
  }
}
.home-section-6-user-reviews-container {
  border: 1px solid #000;
  width: 200px;
  height: 200px;
  display: flex;
  flex-direction: column;
  color: #000;
  background-color: #fff;
  padding: 30px 20px;
  gap: 20px;
  cursor: pointer;
  transition: all 0.8s;
}
.home-section-6-user-reviews-container:hover {
  // transform: scale(1.1);
}
.home-section-6-user-reviews-wrap {
  display: flex;
  justify-content: space-between;
}
.home-section-6-user-reviews-image {
  display: flex;
  gap: 20px;
}
.home-section-6-user-name {
  display: flex;
  flex-direction: column;
  font-size: 20px;
}
.section-6-months {
  font-size: 14px;
  color: #777777;
}
.section-6-description {
  overflow: auto;
  padding: 10px;
}

.section-6-description::-webkit-scrollbar {
  width: 4px;
  background-color: #000;
  border-radius: 1px;
}
.section-6-description::-webkit-scrollbar-thumb {
  width: 4px;
  background-color: #000;
}
.section-6-description::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgb(0 0 0 / 30%);
  background-color: #fff;
}

