.home-section-4 {
  padding: 100px 40px 100px;
  // background-color: #000;
  // background-color: #fff;
  background-color: red;
  align-items: center;
  position: relative;
}
.home-section-4-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
}
.section-4-frame {
  position: relative;
  width: 80vw;
  height: 500px;
  // border-radius: 2px;
  // box-shadow: 1px 2px 10px 0px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  background: #333;
  color: #fff;
}
.section-4-boxes {
  background: #fff;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  width: 100%;
  height: 100%;

  .box {
    position: relative;
    width: 20%;
    transition: all 1s ease-in-out;
    cursor: pointer;
    overflow: hidden;
  }
  .section-4-img-1 {
    background: url(../../../../assets/gallery-1.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-2 {
    background: url(../../../../assets/gallery-2.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-3 {
    background: url(../../../../assets/gallery-3.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-4 {
    background: url(../../../../assets/gallery-4.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-5 {
    background: url(../../../../assets/gallery-5.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-6 {
    background: url(../../../../assets/gallery-6.jpg);
    background-size: cover;
    background-position: center;
  }
  .section-4-img-7 {
    background: url(../../../../assets/markantalya.jpg);
    background-size: cover;
    background-position: center;
  }
}
.hoverfunc:hover {
  width: 100%;
  cursor: url(../../../../assets/plus.svg), nesw-resize;
}
.home-section-4-i:hover {
  transform: scale(1.01);
}
@media (max-width: 1366px) {
  .home-section-4 {
    padding: 120px 30px !important;
  }
  .home-section-4-wrapper {
    gap: 40px !important;
  }
  .home-section-4-i {
    gap: 40px !important;
    img {
      width: 276px !important;
      height: 209px !important;
    }
  }
}
@media (max-width: 1200px) {
  .home-section-4 {
    padding: 100px 20px !important;
  }
  .home-section-4-wrapper {
    gap: 20px !important;
  }
  .home-section-4-i {
    gap: 20px !important;
    img {
      width: 236px !important;
      height: 189px !important;
    }
  }
}
@media (max-width: 944px) {
  .home-section-4 {
    padding: 80px 10px !important;
  }
  .home-section-4-wrapper {
    gap: 10px !important;
  }
  .section-4-frame {
    width: 600px;
    height: 240px;
  }
}
@media (max-width: 704px) {
