.contact {
  padding: 40px 100px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  .contact-heading {
    // font-size: 44px;
    align-self: center;
    font-weight: 500;

    font-size: 52px !important;
    span {
      color: #ba9e66;
    }
  }
  .contact-data {
    font-size: 28px !important;
    color: #ba9e66;
    font-weight: 700;
  }
  .contact-container {
    display: grid;
    // flex-direction: row-reverse;
    grid-template-columns: 1fr 1fr;
  }
  .contact-wrap {
    // background-color: red;
    padding: 40px 60px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 80px;
    gap: 20px;
  }
  .contact-wrap-container {
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: flex-start;
  }
  .contact-num {
    padding: 10px 20px;
    // font-family: "Proza Libre", sans-serif;
    background-color: #000;
    color: #fff;
    display: flex;
    font-size: 28px !important ;
  }
  .contact-cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 20px;
  }

  .contact-data-desc {
    color: #777;
    font-weight: 500;
  }
  .contact-container-2 {
  }
  .contact-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    justify-content: center;
    // border: 1px solid black;
  }
  .contact-form-in {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 10px;
    label {
      font-size: 24px !important;
      span {
        color: red;
      }
    }
    input {
      font-family: "Proza Libre", sans-serif;
      padding: 14px 20px;
      // border: none;
      // border-bottom: 1px solid black;
      // padding: 10px 20px 20px 10px !important;
      width: 100%;
      font-size: 18px;
    }
    input:focus {
      outline: none;
    }
  }
  .form-in-last {
    display: flex;
    textarea {
      font-size: 18px;
      padding: 14px 20px;
      width: 100%;
      resize: vertical;
    }
    textarea:focus {
      outline: none;
    }
  }
}
.contact-button {
  border: none;
  font-family: "Cormorant", serif;
  cursor: pointer;
  text-align: center;
  padding: 20px 40px;
  font-size: 24px;
  font-weight: 700;
  margin-left: 35px;
  background-color: #000000;
  color: #fff;
  position: relative;
  z-index: 1;
}
.contact-button::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scaleY(0);
  transform-origin: center bottom;
  background-color: #ba9e66;
  z-index: -1;
  transition: transform 0.3s;
}

.contact-button:hover::after {
  transform: scaleY(1);
  color: #000;
}

