.blog {
  padding: 0 40px 200px;
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  gap: 40px;
  // background-color: #ba9e66;
  // color: #fff;
  .blog-header {
    font-size: 52px;
    padding: 60px 60px 20px;
    font-weight: 500;
    text-align: center;
  }
  .blog-wrap {
    display: grid;
    padding-left: 60px;
    padding-right: 60px;
    grid-template-columns: 800px 1fr;
    grid-template-rows: 500px;
    // display: flex;
  }
  .blog-wrap-image {
    padding: 0;
    width: 800px;
    height: 500px;
    background-image: url(../../../assets/art-lasovsky-8XddFc6NkBY-unsplash.jpg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }
  .blog-content {
    // background-color: red;
    padding-top: 40px;
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    .blog-content-1 {
      &:hover {
        cursor: pointer;
        .blog-content-header {
          background-size: 100% 2px;
        }
      }
      .blog-content-image {
        width: 140px;
        height: 100px;
        background-color: black;
        background-image: url(../../../assets/aslihan-altin-OqOD9YyDYSQ-unsplash.jpg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
      }
      .blog-content-header {
        font-size: 28px;
        background-image: linear-gradient(currentColor, currentColor);
        background-position: 0 100%;
        background-repeat: no-repeat;
        background-size: 0 2px;
        transition: background-size 0.3s;
        position: relative;
      }
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      gap: 230px;
      justify-content: flex-end;
      padding: 20px 30px 20px;
      border-bottom: 1px solid #000;
    }
    .blog-content-2 {
      &:hover {
        cursor: pointer;
        .blog-content-header {
          background-size: 100% 2px;
        }
      }
      .blog-content-image-2 {
        align-self: flex-end;
        width: 140px;
        height: 100px;
        background-color: black;
        background-image: url(../../../assets/home-section-52.jpg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
      }
      .blog-content-header {
        font-size: 28px;
        background-image: linear-gradient(currentColor, currentColor);
        background-position: 0 100%;
        background-repeat: no-repeat;
        background-size: 0 2px;
        transition: background-size 0.3s;
        position: relative;
      }
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      justify-content: space-evenly;
      border-bottom: 1px solid #000;
      padding-bottom: 20px;
      padding-top: 20px;
    }
  }
  // .blog-wrap {
  //   // border-top: 4px solid #000;
  //   padding-top: 60px;
  //   display: flex;
  //   gap: 40px !important;

  //   .blog-content {
